.projects {
  display: flex;
  text-align: start;
  height: 100vh;
  margin-top: 10vh;
  line-height: 2rem;
  align-items: center;
  justify-content: center;
}

.projects #content {
  height: 100vh;
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects h1 {
  font-size: 3rem;
  color: #fff;
}

.projects p {
  font-size: 1.2rem;
  color: #94a4b4;
}
